<template>
	<div class="m-l-50 m-t-30 w-900">
        <!-- <div style="color:gray;font-size:14px;">
            <ul>
                <li style="list-style: initial;">logo</li>
                <li style="list-style: initial;">CDN</li>
            </ul>
            <ul>
                <li style="list-style: initial;"></li>
                <li style="list-style: initial;"></li>
                <li style="list-style: initial;">logo</li>
            </ul>
        </div> -->
		<el-form ref="form" :model="form" :rules="rules" label-width="130px">
			<el-form-item label="" prop="name">
				<el-input v-model.trim="form.name" class="h-40 w-200"></el-input>
			</el-form-item>        
            <el-form-item label="logo">
              <el-upload
              :action="uploadUrl"
              type="drag"
              limit="1"
              :on-exceed="onexceed"
              :thumbnail-mode="true"
              :on-remove="handleRemove"
              :on-success="uploadSuccess"
              :on-error="uploadFail"
              :default-file-list="fileList">
                <i class="el-icon-upload"></i>
                <div class="el-dragger__text"><em></em></div>
                <div class="el-upload__tip" slot="tip">jpg/png</div>
              </el-upload>
            </el-form-item>
        <el-form-item label="" prop="logo">
          <el-input v-model.trim="form.logo" class="h-40 w-200" placeholder="" readonly></el-input>
          <!-- <a href="http://wantu.taobao.com/mediaportal/index.htm" target="_blank" class="btn-link el-button el-button--success"> []</a> -->
        </el-form-item>

            <el-form-item label="" prop="maxmoney">
                <el-input v-model.trim="form.maxmoney" class="h-40 w-200"></el-input>
            </el-form-item>
            <el-form-item label="" prop="fddays">
                <el-input v-model.trim="form.fddays" class="h-40 w-200"></el-input>
            </el-form-item>
            <el-form-item label="" prop="flday">
                <el-input v-model.trim="form.flday" class="h-40 w-200"></el-input>
            </el-form-item>
            <el-form-item label="" prop="limitday">
                <el-input v-model.trim="form.limitday" class="h-40 w-200"></el-input>
            </el-form-item>
            <el-form-item label="" prop="squrl">
                <el-input v-model.trim="form.squrl" class="h-40 w-200" placeholder="http://xxx"></el-input>
            </el-form-item>
			<el-form-item label="">
        <el-input
          type="textarea"
          class="w-200"
          :autosize="{ minRows: 2, maxRows: 4}"
          placeholder=""
          v-model="form.remark">
        </el-input>
      </el-form-item>
			<el-form-item>
				<el-button type="primary" @click="add('form')" :loading="isLoading"></el-button>
				<el-button @click="goback()"></el-button>
			</el-form-item>
		</el-form>
		<!-- <preview ref="preview" :url="propsImg"></preview> -->
	</div>
</template>
<script>
  import http from '../../../../assets/js/http'
  import fomrMixin from '../../../../assets/js/form_com'
  import preview from '../../system/config/preview.vue'

  export default {
    data() {
      return {
        isLoading: false,
        fileList: [],
        propsImg: '',
        uploadUrl: '',
        form: {
          name: '',
          logo: '',
          fddays: 0,
          flday: '0.5',
          maxmoney: 0,
          limitday: '',
          squrl: '',
          remark: ''
        },
        rules: {
          name: [
            { required: true, message: '', trigger: 'blur' }
          ]
        }
      }
    },
    methods: {
      add(form) {
        this.$refs[form].validate((valid) => {
          if (valid) {
            this.isLoading = !this.isLoading
            this.apiPost('admin/posts', this.form).then((res) => {
              this.handelResponse(res, (data) => {
                _g.toastMsg('success', '')
                setTimeout(() => {
                  this.goback()
                }, 1500)
              }, () => {
                this.isLoading = !this.isLoading
              })
            })
          }
        })
      },
      uploadSuccess(res, file, fileList) {
        this.form.logo = res.data
        let data = {
          name: '',
          url: window.HOST + res.data
        }
        if (this.fileList.length) {
          this.fileList[0] = data
        } else {
          this.fileList.push(data)
        }
      },
      onexceed(){
        _g.toastMsg('warning','');
      },
      uploadFail(err, res, file) {
        console.log('err = ', _g.j2s(err))
        console.log('res = ', _g.j2s(res))
      },
      handleRemove(file, fileList) {
        console.log('file = ', file)
        console.log('fileList = ', fileList)
        this.form.logo = ''
      },
      viewPic() {
        this.propsImg = this.fileList[0].url
        this.$refs.preview.open()
      }
    },
    created() {
      this.uploadUrl = window.HOST + '/Upload'
      _g.closeGlobalLoading()
    },
    components: {
      preview
    },
    mixins: [http, fomrMixin]
  }
</script>


